<script>
	import Candidates from './Candidates.svelte';
	import { fade } from 'svelte/transition';
	import { SUDOKU_SIZE } from '@sudoku/constants';
	import { cursor } from '@sudoku/stores/cursor';
	import { strategyGrid } from '@sudoku/stores/grid';

	export let value;
	export let cellX;
	export let cellY;
	export let candidates;
	export let explore;

	export let disabled;
	export let conflictingNumber;
	export let userNumber;
	export let selected;
	export let sameArea;
	export let sameNumber;
	export let strategyCell;
	export let relativeCell;
	export let invalidCandidate;

	const borderRight = (cellX !== SUDOKU_SIZE && cellX % 3 !== 0);
	const borderRightBold = (cellX !== SUDOKU_SIZE && cellX % 3 === 0);
	const borderBottom = (cellY !== SUDOKU_SIZE && cellY % 3 !== 0);
	const borderBottomBold = (cellY !== SUDOKU_SIZE && cellY % 3 === 0);
</script>

<div class="cell row-start-{cellY} col-start-{cellX}"
     class:border-r={borderRight}
     class:border-r-4={borderRightBold}
     class:border-b={borderBottom}
     class:border-b-4={borderBottomBold}>
	{#if !disabled}
		<div class="cell-inner"
		     class:user-number={userNumber}
		     class:selected={selected}
		     class:same-area={sameArea}
		     class:same-number={sameNumber}
		     class:conflicting-number={conflictingNumber}
			 class:strategy-cell={strategyCell}
			 class:relative-cell={relativeCell}
			 class:invalid-candidate={invalidCandidate}
		>
			<button class="cell-btn" on:click={cursor.set(cellX - 1, cellY - 1)}>
				{#if candidates.length > 1 && (strategyCell || relativeCell)}
					<Candidates {candidates} gridRow={cellY - 1} gridCol={cellX - 1}/>
				{:else if candidates.length ===  1 && relativeCell}
					<span class="cell-text">{candidates[0]} </span>
				{:else if candidates.length === 0 && strategyCell}
					<span class="cell-text">0</span>
				{:else}
					<span class="cell-text">{explore || value || ''} </span>
				{/if}
			</button>

		</div>
	{/if}

</div>

<style>
	.cell {
		@apply h-full w-full row-end-auto col-end-auto;
	}

	.cell-inner {
		@apply relative h-full w-full text-gray-800;
	}

	.cell-btn {
		@apply absolute inset-0 h-full w-full;
	}

	.cell-btn:focus {
		@apply outline-none;
	}

	.cell-text {
		@apply leading-full text-base;
	}

	@media (min-width: 300px) {
		.cell-text {
			@apply text-lg;
		}
	}

	@media (min-width: 350px) {
		.cell-text {
			@apply text-xl;
		}
	}

	@media (min-width: 400px) {
		.cell-text {
			@apply text-2xl;
		}
	}

	@media (min-width: 500px) {
		.cell-text {
			@apply text-3xl;
		}
	}

	@media (min-width: 600px) {
		.cell-text {
			@apply text-4xl;
		}
	}

	.user-number {
		@apply text-primary;
	}

	.selected {
		@apply bg-primary text-white;
	}

	.same-area {
		@apply bg-primary-lighter;
	}

	.same-number {
		@apply bg-primary-light;
	}

	.conflicting-number {
		@apply text-red-600;
	}

	.strategy-cell {
		@apply bg-green-400 text-green-200;
	}

	.relative-cell {
		@apply bg-blue-600 text-white;
	}

	.invalid-candidate {
		@apply text-red-600;
	}
</style>